@import "https://fonts.googleapis.com/css2?family=Urbanist:wght@100;300;600&display=swap";
.loader-wrapper {
  z-index: 100000;
  width: 100vw;
  height: 100vh;
  background: #000c;
  position: fixed;
  top: 0;
  left: 0;
}

.loader-wrapper #loader-status {
  z-index: 2;
  color: #fff;
  width: 300px;
  text-align: center;
  font-size: 16px;
  position: absolute;
  top: calc(50% + 35px);
  left: 50%;
  transform: translateX(-150px);
}

.loader-wrapper .loader-spinner {
  z-index: 1;
  width: 60px;
  height: 60px;
  border: 16px solid #bbb;
  border-top-color: #7367f0;
  border-radius: 50%;
  margin: -38px 0 0 -38px;
  animation: spin 2s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

